@-webkit-keyframes spacify {
  from {
    letter-spacing: 0px;
    opacity: 0;
  }

  to {
    letter-spacing: 14px;
    opacity: 1;
  }
}

@keyframes spacify {
  from {
    letter-spacing: 0px;
    opacity: 0;
  }

  to {
    letter-spacing: 14px;
    opacity: 1;
  }
}

@-webkit-keyframes spacifyTagline {
  from {
    letter-spacing: 4px;
    opacity: 0;
  }

  to {
    letter-spacing: 0px;
    opacity: 1;
  }
}

@keyframes spacifyTagline {
  from {
    letter-spacing: 4px;
    opacity: 0;
  }

  to {
    letter-spacing: 0px;
    opacity: 1;
  }
}

