.buttonTopMargin {
  position: absolute;
  margin-bottom: 120px;
}

#titleButtons {
  margin: 10px 0;
}

.bn632-hover {
  width: 150px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 55px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:hover {
  background-position: 100% 0;
  moz-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:focus {
  outline: none;
}

.bn632-hover.bn25 {
  background-image: linear-gradient(to right,
      #29323c,
      #485563,
      #2b5876,
      #4e4376);
  box-shadow: 0 4px 15px 0 rgba(33, 39, 47, 0.75);
  margin-top: 5vh;
}
#navbar {
  background: rgba(45, 45, 45, 0.7);
  position: fixed;
  top: 0;
  width: 100%;
  padding-left: 10px;
  padding-top: 4px;
  padding-bottom: 4px;
  /* To make sure it is on top of any other content */
  z-index: 100;
}

#navbar a {
  float: left;
  display: block;
  padding-left: 12px;
  padding-right: 12px;
  padding-top: 7px;
  padding-bottom: 4px;
  border-radius: 4px;
}

#navbar a:hover {
  background-color: rgb(71, 71, 71);
}
#separator {
  width: 80%;
  margin-left: 10%;
  border-bottom: solid 1px rgba(163, 163, 163, 0.4);
  margin-top: 20px;
  margin-bottom: 40px;
}
