@import url('https://fonts.googleapis.com/css?family=Montserrat');
@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

#titleHead {
  font-family: "Montserrat";
  text-align: center;
  color: #FFF;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin-bottom: 0px;
  margin-left: 15px;
  margin-right: 15px;
}

h1 {
  background-image: url(https://media.giphy.com/media/10Ua7rs9fxa8QE/giphy.gif?cid=ecf05e47wpm625w7zv2bk68daxd20otskh4wyp5ap44k4n2z&rid=giphy.gif&ct=g);
  background-size: cover;
  color: transparent;
  -moz-background-clip: text;
  -webkit-background-clip: text;
  text-transform: lowercase;
  font-size: 130px;
  margin: 10px 0;
  animation: spacify 2s ease;
  letter-spacing: 14px;

}

#tagline {
  font-size: 24px;
  font-family: 'Poppins', sans-serif;
  font-weight: lighter;
  color: rgb(171, 171, 171);
  animation: spacifyTagline 2s ease;
}

.taglineHighlights {
  /* background: linear-gradient(-45deg, #23d5ab, #23a6d5, #e73c7e, #ee7752);
  background-size: 500%;
  color: transparent;
  -moz-background-clip: text;
  -webkit-background-clip: text;
  animation: gradient 16s ease infinite; */
  color: #FFF;

}

@keyframes spacify {
  from {
    letter-spacing: 0px;
    opacity: 0;
  }

  to {
    letter-spacing: 14px;
    opacity: 1;
  }
}

@keyframes spacifyTagline {
  from {
    letter-spacing: 4px;
    opacity: 0;
  }

  to {
    letter-spacing: 0px;
    opacity: 1;
  }
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.buttonTopMargin {
  position: absolute;
  margin-bottom: 120px;
}

#titleButtons {
  margin: 10px 0;
}

.bn632-hover {
  width: 150px;
  font-size: 16px;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  margin: 20px;
  height: 55px;
  text-align: center;
  border: none;
  background-size: 300% 100%;
  border-radius: 50px;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:hover {
  background-position: 100% 0;
  moz-transition: all .4s ease-in-out;
  -o-transition: all .4s ease-in-out;
  -webkit-transition: all .4s ease-in-out;
  transition: all .4s ease-in-out;
}

.bn632-hover:focus {
  outline: none;
}

.bn632-hover.bn25 {
  background-image: linear-gradient(to right,
      #29323c,
      #485563,
      #2b5876,
      #4e4376);
  box-shadow: 0 4px 15px 0 rgba(33, 39, 47, 0.75);
  margin-top: 5vh;
}